import { Value, Node, Block } from '@ali/4ever-cangjie';
import { isInSameList } from '../../utils/isInSameList';

export default (value: Value, currrentItem: Node | null): string[] => {
  const result: string[] = [];
  if (!currrentItem) return result;
  const { key } = currrentItem;
  const currentList = (currrentItem as Block).data.list;
  if (!key || !currentList) return [];
  const { listId } = currentList;
  const { document } = value;
  const root = document.getParent(key) || document;
  const nodesLength = root?.nodes.length || 0;
  for (let i = 0; i < nodesLength; i++) {
    const node = root?.nodes[i] as Block;
    const listConfig = node.data.list;
    if (!isInSameList(listConfig, listId)) continue;
    result.push(node.key);
  }
  return result;
};
